@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
.main {
  font-family: 'Poppins', sans-serif;
  max-width: 1920px;
  height: 1px;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.home__title {
  margin: 0 0 60px;
  font-size: 48px;
  line-height: 1.2;
}

.home__button {
  margin: 0;
  padding: 0;
  width: 300px;
  height: 100px;
  box-sizing: border-box;
}

.home {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.map__left-column {
  width: 30%;
  border-right: #000 1px solid;
  overflow: scroll;
}

.map__right-column {
  width: 70%;
}

.map {
  display: flex;
  height: 100%;
  max-height: 100vh;
  width: 100%;
  overflow: scroll;
}



